import React, { Component } from 'react'
import './CountupAddax.scss'
import CountUpA  from './CountUpA/CountUpA'

export default class CountupAddax extends Component {

    state={
        countAddax:[
            {icon:'simple-smile' , countNum:500 ,title:'Happy Clients'},
            {icon:'shield-alt' , countNum:980 ,title:'Completed Projects'},
            {icon:'trophy' , countNum:46 ,title:'Wining Awards'},
            {icon:'users' , countNum:50 ,title:'Team Members'}
        ]
        
    }
  

    render() {
              
        return (
            <div className="countup-addax " id="countUp-add">
            <div className="overlay padding">
                <div className="container"> 
                    <CountUpA countAddax={this.state.countAddax}/>                    
                </div>
            </div>
        </div>
        )
    }
}
