import React from 'react'
import ListItems from '../../../UI/ListItems/ListItems'
import './ItemShuffle.scss'

const ItemShuffle = (props) => {
    return (
        <div className="item-shuffle">
            <div className="row">
                <div className="col-md-6">
                    <div className="item-cont">
                        <h3>{props.itemShuffle[props.id].title}</h3>
                        <p>{props.itemShuffle[props.id].cont}</p>
                        <ListItems responsive={props.responsive} items={props.itemShuffle[props.id].items}/>
                        <button className="btn mainButton" type="button">Read More</button>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="image">
                        <img src={props.itemShuffle[props.id].img} alt="img"/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ItemShuffle
