import React from 'react'
import ListItems from '../../../UI/ListItems/ListItems'
import './PricingItem.scss'

const PricingItem = (props) => {
    return (
        <div className="pricing-item text-center">
            <div className="title">
                <h3>{props.title}</h3>
            </div>

            <div className="price">
                <p><sup>$</sup>{props.price}</p>
                <span>/Per Month</span>
            </div>
            <hr/>
            
            <ListItems items={props.items} responsive={props.responsive}/>
            <hr/>

            <div className="btn mainButton">Oreder Now</div>
        </div>
    )
}

export default PricingItem
