import React, { Component } from 'react'
import partner1 from '../../../images/PartnerArea/partner1.png'
import partner2 from '../../../images/PartnerArea/partner2.png'
import partner3 from '../../../images/PartnerArea/partner3.png'
import partner4 from '../../../images/PartnerArea/partner4.png'
import partner5 from '../../../images/PartnerArea/partner5.png'
import partner6 from '../../../images/PartnerArea/partner6.png'
import Slider from "react-slick"
import './PartnerArea.scss'


export default class PartnerArea extends Component {

    state={
        partners:[partner1,partner2,partner3,partner4,partner5,partner6,partner1,partner2,partner3,partner4,partner5,partner6]
    }


    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 1000,
            autoplay: true,
            autoplaySpeed: 5000,
            slidesToShow: 6,
            slidesToScroll: 6,
            responsive: [
              {
                breakpoint: 1024,
                settings: {
                  slidesToShow: 6,
                  slidesToScroll: 6,
                  autoplay: true,
                  autoplaySpeed: 5000,
                  infinite: true,
                  dots: true
                }
              },
              {
                breakpoint: 992,
                settings: {
                  slidesToShow: 4,
                  slidesToScroll: 4,
                  autoplay: true,
                  autoplaySpeed: 5000,
                  initialSlide: 2
                }
              },
              {
                breakpoint: 650,
                settings: {
                  slidesToShow: 3,
                  slidesToScroll: 3,
                  autoplay: true,
                  autoplaySpeed: 5000
                }
              }
            ]
          };

        return (
            <div className="partner-area padding">
                <div className="container">
                    <div className="partner-slider ">
                        <Slider {...settings} >
                            {
                                this.state.partners.map(partner=>{
                                return(
                                    
                                    <div className="partnerImage" key={Math.random()}>
                                        <img src={partner} alt="partnerImage"/>
                                    </div>
                                    
                                )
                            })
                            }
                        </Slider>
                     </div>
                    
                </div>
            </div>
        )
    }
}
