import React, { Component } from 'react'
import './AskedQuestion.scss'
import askedQuestionImg from '../../../images/askedQuestion.png'
import QuestionItem from './QuestionItem/QuestionItem'

export default class AskedQuestion extends Component {

    state={
        questions:[
            {index:0,question:'What harsh truths do you prefer to ignore?',
             answer:'Exercitation in fugiat est ut ad ea cupidatat ut in cupidatat occaecat ut occaecat consequat est minim minim esse tempor laborum consequat esse adipisicing eu reprehenderit enim.',
             arrowDown:'rounded-down' , arrowRight:'rounded-right'},
             
             {index:1, question:'Is free will real or just an illusion?',
             answer:'In ad velit in ex nostrud dolore cupidatat consectetur ea in ut nostrud velit in irure cillum tempor laboris sed adipisicing eu esse duis nulla non.' ,
            arrowDown:'rounded-down' , arrowRight:'rounded-right'},
            
             {index:2, question:'Is free will real or just an illusion?',
             answer:'In ad velit in ex nostrud dolore cupidatat consectetur ea in ut nostrud velit in irure cillum tempor laboris sed adipisicing eu esse duis nulla non.' ,
            arrowDown:'rounded-down' , arrowRight:'rounded-right'},
            
             {index:3, question:'Is free will real or just an illusion?',
             answer:'In ad velit in ex nostrud dolore cupidatat consectetur ea in ut nostrud velit in irure cillum tempor laboris sed adipisicing eu esse duis nulla non.' ,
            arrowDown:'rounded-down' , arrowRight:'rounded-right'},
            
             {index:4, question:'Is free will real or just an illusion?', 
            answer:'In ad velit in ex nostrud dolore cupidatat consectetur ea in ut nostrud velit in irure cillum tempor laboris sed adipisicing eu esse duis nulla non.' ,
        arrowDown:'rounded-down' , arrowRight:'rounded-right'}
        ],
        arrowButton:['rounded-right','rounded-down'],
        clicked:false,
        id:0
    }


    openAnswer=(index)=>{
        this.setState({
            id:index
        })
    }

    render() {
        return (
            <div className="asked-question padding">
                <div className="container">
                    <div className="askedQuestion-cont cont">
                        <h2>Frequently Asked <span>Question</span></h2>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor<br/> incididunt ut labore et dolore magna aliqua.</p>
                    </div>

                    <div className="askedQuestion-accordion">
                        <div className="row">
                            <div className="col-lg-7">
                                <div className="question-items">
                                  {
                                    this.state.questions.map(quesItem=>{
                                        return <QuestionItem key={quesItem.index} question={quesItem.question} 
                                        answer={quesItem.answer} openAnswer={this.openAnswer} index={quesItem.index} 
                                        id={this.state.id} arrowDown={quesItem.arrowDown} arrowRight={quesItem.arrowRight}/>
                                    })
                                  }
                                    
                                </div>
                            </div>
                            <div className="col-lg-5">
                                <img src={askedQuestionImg} alt="askedQuestionImg" />
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
        )
    }
}
