import React,{Component} from "react";
import Slider from "react-slick";
import Mohamed from '../../../../images/team/1.jpg'
import Ahmed  from '../../../../images/team/2.jpg'
import Yousef from '../../../../images/team/3.jpg'
import Osama  from '../../../../images/team/4.jpg'
import  Jony  from '../../../../images/team/5.jpg'
import  Omar from '../../../../images/team/6.jpg'
import Icon from 'react-icofont'
import './TeamSlider.scss'

 
export default class TeamSlider extends Component {

    state ={
        teamData:[
            {name:'Mohamed Adel', job:'Web Developer' , image:Mohamed , socialMedia:['facebook' , 'twitter' , 'instagram' , 'linkedin']},
            {name:'Ahmed Khaled', job:'Front-end Developer' , image:Ahmed , socialMedia:['facebook' , 'twitter' , 'instagram' , 'linkedin']},
            {name:'Yousef Ahmed', job:'Back-end Developer' , image:Yousef , socialMedia:['facebook' , 'twitter' , 'instagram' , 'linkedin']},
            {name:'Osama Hassen', job:'designer' , image:Osama , socialMedia:['facebook' , 'twitter' , 'instagram' , 'linkedin']},
            {name:'Jony Gorg', job:'Photography' , image:Jony , socialMedia:['facebook' , 'twitter' , 'instagram' , 'linkedin']},
            {name:'Omar Adam', job:'Web DEveloper' , image:Omar , socialMedia:['facebook' , 'twitter' , 'instagram' , 'linkedin']}

        ] 
    } 

  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 1000,
      autoplay: true,
      autoplaySpeed: 5000,
      slidesToShow: 3,
      slidesToScroll: 3,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            autoplay: true,
            autoplaySpeed: 5000,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 992,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            autoplay: true,
            autoplaySpeed: 5000,
            initialSlide: 2
          }
        },
        {
          breakpoint: 650,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 5000
          }
        }
      ]
    };
    return (
        <div className="team-slider text-center">
         <Slider {...settings} >
            {
                this.state.teamData.map(teamD=>{
                  return(
                    
                      <div className="team-s" key={Math.random()}>
                        <div className="over">

                          <div className="icons">
                            {teamD.socialMedia.map(social=>{
                                return <Icon icon={social} className="icon" key={Math.random()}/>
                                })} 
                          </div>
                          <div className="data">
                            <h5>{teamD.name}</h5>
                            <span>{teamD.job}</span>
                          </div> 
                          
                        </div>

                        <div className="image">
                          <img src={teamD.image} alt="team-image" />    
                        </div>
                      </div>
                    
                  )
              })
            }
          </Slider>
        </div>
      
      
    );
  }
}