import React, { Component } from 'react'
import Card from '../../../UI/Card/Card'
import WebDesign from '../../../../images/services/s1.jpg'
import Branding from '../../../../images/services/s2.jpg'
import Marketing from '../../../../images/services/s3.jpg'
import Analytics from '../../../../images/services/s4.jpg'
import Development from '../../../../images/services/s5.jpg'
import UiUxDesign from '../../../../images/services/s6.jpg'
import ResponsiveDesign from '../../../../images/services/s7.jpg'
import Photography from '../../../../images/services/s8.jpg'
import CustomSupport from '../../../../images/services/s9.jpg'
import Icon from 'react-icofont'
import './ServicesCards.scss'

export default class ServicesCards extends Component {

    state={
        serCard:[
            {image:WebDesign , icon:'pencil-alt-1' , title:'Web Design', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.'},
            {image:Branding , icon:'rolling-eyes' , title:'Branding', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.'},
            {image:Marketing , icon:'contrast' , title:'Marketing', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.'},
            {image:Analytics , icon:'chart-growth', title:'Analytics', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.' },
            {image:Development , icon:'anchor' , title:'Development', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.'},
            {image:UiUxDesign , icon:'headphone-alt-3' , title:'Ui & UxDesign', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.'},
            {image:ResponsiveDesign , icon:'stock-mobile' , title:'Responsive Design', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.'},
            {image:Photography , icon:'ui-camera' , title:'Photography', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.'},
            {image:CustomSupport , icon:'life-buoy' ,title:'CustomSupport', cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.' }
        ]
        
    }
    render() {
        return (
            
                <div className="row">
                    
                        {
                            this.state.serCard.map(imCard=>{
                                
                                return (
                                    <div className="col-lg-4 col-md-6 s-card" key={Math.random()}>
                                      <Card title={imCard.title} cont={imCard.cont}>
                                        <div className="img-card" >
                                            <div className="over">
                                                <Icon icon={imCard.icon} className="img-icon" size="2"/>
                                            </div>
                                            <img src={imCard.image} alt="serveciesImage" style={{width:'100%'}}/>
                                        </div>
                                        </Card>
                                        
                                    </div>
                                )
                            })
                        }
                    
                </div>
            
        )
    }
}
