import React, { Component,createRef } from 'react'
import work1 from '../../../images/ourWork/work-1.jpg'
import work2 from '../../../images/ourWork/work-2.jpg'
import work3 from '../../../images/ourWork/work-3.jpg'
import work4 from '../../../images/ourWork/work-4.jpg'
import work5 from '../../../images/ourWork/work-5.jpg'
import work6 from '../../../images/ourWork/work-6.jpg'
import mixitup from 'mixitup';
import './OurWork.scss'
import ScrollableAnchor from 'react-scrollable-anchor'

export default class OurWork extends Component {

    constructor(props){
        super(props);
        this.containerEl=createRef();
    }

    componentDidMount(){
        mixitup('.img-shuffle .row');
    }
    render() {
        

        return (
            <ScrollableAnchor id={'Work'}>
                <div className="our-work padding" id="ourWork-a">
                    <div className="container">
                        <div className="ourWork-cont cont">
                            <h2>Our <span>Work</span></h2>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor<br/> incididunt ut labore et dolore magna aliqua.</p>
                        </div>

                        <div className="ourWork-shuffle">

                            <div className="shuffle-buttons">
                                <button className="btn" data-filter="all">All</button>
                                <button className="btn" data-filter=".brand">Brand</button>
                                <button className="btn" data-filter=".design">Design</button>
                                <button className="btn" data-filter=".graphic">Graphic</button>
                                <button className="btn" data-filter=".photoshop">Photoshop</button>
                                <button className="btn" data-filter=".illustrator">Illustrator</button> 
                            </div>
                            
                            <div className="img-shuffle">
                                <div className="row" ref={this.containerEl}>
                                    <div className="col-md-4 col-sm-6 mix brand">  <img  src={work1} alt="img1"/>  </div>
                                    <div className="col-md-4 col-sm-6 mix design">  <img  src={work2} alt="img2"/>  </div>
                                    <div className="col-md-4 col-sm-6 mix graphic">  <img  src={work3} alt="img3"/>  </div>
                                    <div className="col-md-4 col-sm-6 mix photoshop">  <img  src={work4} alt="img4"/>  </div>
                                    <div className="col-md-4 col-sm-6 mix design">     <img  src={work5} alt="img5"/>  </div>
                                    <div className="col-md-4 col-sm-6 mix illustrator">  <img  src={work6} alt="img6"/>  </div>
                                </div>
                                
                            </div>

                        </div>
                        
                    </div>
                </div>
            </ScrollableAnchor>
        )
    }
}
