import React from 'react'
import Icofont from 'react-icofont'
import FormContact from './FormContact/FormContact'
import './ContactUs.scss'
import ScrollableAnchor from 'react-scrollable-anchor'


const ContactUs = (props) => {
    return (
        <ScrollableAnchor id={'Contact'}>
            <div className="contact-us padding" id="contactUs-a">
                <div className="container">
                    <div className="contactUs-cont cont">
                    <p className="sp">Get In Touch</p>
                        <h2>Let's <span>Contact</span> Us</h2>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                    </div>
    
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="map">
                                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2035.4215400021346!2d18.081945815979626!3d59.32591578165764!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x465f9d5641a77669%3A0xe02744b36aab9f53!2sExercisplan+4%2C+111+49+Stockholm%2C+Sweden!5e0!3m2!1sen!2ssa!4v1560815275290!5m2!1sen!2ssa" width="100%" height="400px" frameBorder="0" style={{border:0}} allowFullScreen></iframe>
                                <div className="map-details">
                                    <span></span> <br/>
                                    <ul className="list-unstyled">
                                        <li><Icofont className="icon" icon="location-pin" /> <span>Exercisplan 4, 111 49 Stockholm, Sweden</span></li>
                                        <li><Icofont className="icon" icon="email" /><span>addax@gmail.com</span></li>
                                        <li><Icofont className="icon" icon="ui-dial-phone" /><span>(+124)412-2445515</span></li>
                                        <li><Icofont className="icon" icon="fax" /><span>617-241-60055</span></li>
                                    </ul>                        
                                </div>
                            </div>
                        </div>
                        
                        <div className="col-lg-6">
                            <div className="contact-form">
                                <FormContact />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </ScrollableAnchor>
    )
}

export default ContactUs
