import React, { Component } from 'react'
import Card from '../../../UI/Card/Card'
import './WelcomeCards.scss'

 
export default class WelcomeCards extends Component {

    state={ 
        cardData:[
            {icon:'pencil-alt-1', title:'Creative Design' , cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.'},
            {icon:'laptop-alt', title:'Friendly Codes' , cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.'},
            {icon:'life-buoy', title:'Fast Support' , cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.'}
        ]
        
    }
 
    render() { 
        return (
            <div className="row"> 
                {
                    this.state.cardData.map(data=>{
                        return <div className="col-xl-4  col-md-6 col-12 " key={Math.random()}>
                            <Card icon={data.icon} title={data.title} cont={data.cont} />
                        </div>
                    })
                } 
            </div>
        ) 
    }
}
