import React, { Component , createRef } from 'react'
import SkillImage from '../../../images/skillsAddax.jpg'
import { InView } from 'react-intersection-observer'
import './Skills.scss'
import styled , {keyframes} from 'styled-components'

export default class Skills extends Component {

    state={
        skills:[
            {name:'Branding' , percentage:'92%'} ,
            {name:'Marketing' , percentage:'97%'},
            {name:'Design' , percentage:'90%'},
            {name:'Development' , percentage:'95%'}
        ]
    }

    constructor(props){
        super(props);
     
        this.skillsAddax=createRef();

    }

    
    render() {

        const incresingWidth =keyframes`
             from{width: 0}
             to{width: ${this.state.skills.map(per=>{
                 return per.percentage
             })}}
        `;

        const Bar = styled.div`
            background-color: #ff2c57;
            height: 100%;
            border-radius: 10px;
            width: 0;
            animation: ${incresingWidth} 2s ease-in-out;
        `;
        return (
             
            <div className="skills-addax padding" id="skills-a" ref={this.skillsAddax}>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="skills">
                                <h2>Some of Our <span>Skills</span></h2>
                                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                                
                                <InView triggerOnce="true">
                                {({ inView, ref }) => (
                                     
                                        this.state.skills.map(skill=>{
                                            return <div className="skill" key={Math.random()} ref={ref}>
                                                        <div className="bar-top">
                                                            <h5>{skill.name}</h5> 
                                                            <span>{skill.percentage}</span>
                                                        </div>
                                                        <div className="bar"> 
                                                            <Bar className="s-bar" style={{width: inView ?skill.percentage : '0'}}> </Bar>
                                                        </div>
    
                                                    </div>
                                        })
                                    
                                    )}
                                    
                                </InView>
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="image">
                                <i className="icofont-ui-play"></i>
                                <img  src={SkillImage} alt="skill-img"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
