import React , {Component , createRef} from 'react'
import './NavbarAdax.scss'
import { Navbar, Nav} from 'react-bootstrap';
import { goToAnchor } from 'react-scrollable-anchor'


class NavbarAdax extends Component  { 

    
    constructor(props){
        super(props);
        this.navElement=createRef();
    }

    
    scrollFunction=(e)=>{ 
        if(document.body.scrollTop > 50 || document.documentElement.scrollTop >50 ){
            this.navElement.current.style.backgroundColor='rgba(130, 126, 126, 0.82)';
            this.navElement.current.classList.add("slide-bottom");
 
        }else{
         this.navElement.current.style.backgroundColor='transparent';
         this.navElement.current.classList.remove("slide-bottom");
         this.navElement.current.style.transform="translateY(0)";
        }

        /*

        if((document.body.scrollTop < 50 || document.documentElement.scrollTop <50) 
        && (document.body.scrollTop.offsetWidth>991.98 || document.documentElement.offsetWidth >991.98 )){
            this.navElement.current.style.backgroundColor='transparent';
        }

        if((document.body.scrollTop < 50 || document.documentElement.scrollTop <50) 
        && (document.body.scrollTop.offsetWidth<=991.98 || document.documentElement.offsetWidth <=991.98 )){
            this.navElement.current.style.backgroundColor='#fff'
        }
       

        if((document.body.scrollTop > 50 || document.documentElement.scrollTop >50) 
        && (document.body.scrollTop.offsetWidth>991.98 || document.documentElement.offsetWidth >991.98 )){
            this.navElement.current.style.backgroundColor='rgba(130, 126, 126, 0.82)';
        }

        if((document.body.scrollTop > 50 || document.documentElement.scrollTop >50) 
        && (document.body.scrollTop.offsetWidth<=991.98 || document.documentElement.offsetWidth <=991.98 )){
            this.navElement.current.style.backgroundColor='#fff'
        }

       */
   
      
    }

    
       
    render(){
        
        window.addEventListener('scroll' , this.scrollFunction);

        return (
            
            <div className="addax-navbar " id="a-nav" ref={this.navElement} >
                <div className="container">
                    <Navbar  expand="lg">
                        <Navbar.Brand  href="#" onClick={()=>goToAnchor('Home',false)} ><span>a</span>DDAX</Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="ml-auto" id="nav-n">
                                <Nav.Link href="#" onClick={()=>goToAnchor('Home',false)}>Home</Nav.Link>
                                <Nav.Link href="#" onClick={()=>goToAnchor('About',false)}>About</Nav.Link>
                                <Nav.Link href="#" onClick={()=>goToAnchor('Team',false)}>Team</Nav.Link>
                                <Nav.Link href="#" onClick={()=>goToAnchor('Services',false)}>Services</Nav.Link>
                                <Nav.Link href="#" onClick={()=>goToAnchor('Work',false)}>Work</Nav.Link>
                                <Nav.Link href="#" onClick={()=>goToAnchor('Price',false)}>Price</Nav.Link>
                                <Nav.Link href="#" onClick={()=>goToAnchor('Blog',false)}>Blog</Nav.Link>
                                <Nav.Link href="#" onClick={()=>goToAnchor('Contact',false)}>Contact</Nav.Link>
                            </Nav>
                        </Navbar.Collapse>
                </Navbar>
                 </div>
            </div>
            
        )
    }
    
}

export default NavbarAdax
