import React,{Component} from "react";
import Slider from "react-slick";
import './BlogSlider.scss'
import Card from '../../../UI/Card/Card'
import './BlogSlider.scss'
import card1 from '../../../../images/services/s1.jpg'
import card2 from '../../../../images/services/s2.jpg'
import card3 from '../../../../images/services/s3.jpg'
import card4 from '../../../../images/services/s4.jpg'
import card5 from '../../../../images/services/s5.jpg'


export default class BlogSlider extends Component {

    state ={
        BcardData:[
            {smallTitle:'Business & Tech', titleLink:'The best gear for starting Link small business' , linlRef:'',Hbackground:card1,
            cont:' Fusce vitae nulla at lorem pret ium semper. Curab itur laoreet, lectus ac digni ssim vest ibul lorem pretium semper lectus ac digni...'},
     
            {smallTitle:'Business & Tech', titleLink:'Get your first business suit right with these tips' , linlRef:'',Hbackground:card2,
            cont:' Fusce vitae nulla at lorem pret ium semper. Curab itur laoreet, lectus ac digni ssim vest ibul lorem pretium semper lectus ac digni...'},

            {smallTitle:'Design & IT', titleLink:'How to Make Link Font - Font Design Full Process' , linlRef:'',Hbackground:card3,
            cont:' Fusce vitae nulla at lorem pret ium semper. Curab itur laoreet, lectus ac digni ssim vest ibul lorem pretium semper lectus ac digni...'},

            {smallTitle:'Marketing & Trend', titleLink:'10 Hot Marketing Trends You Need to Implement in 2018' , linlRef:'',Hbackground:card4,
            cont:' Fusce vitae nulla at lorem pret ium semper. Curab itur laoreet, lectus ac digni ssim vest ibul lorem pretium semper lectus ac digni...'},

            {smallTitle:'Business & Tech', titleLink:'How to Be Link Work From Home Professional' , linlRef:'',Hbackground:card5,
            cont:' Fusce vitae nulla at lorem pret ium semper. Curab itur laoreet, lectus ac digni ssim vest ibul lorem pretium semper lectus ac digni...'},
        ] 
    } 

  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 1000,
      autoplay: false,
      autoplaySpeed: 5000,
      slidesToShow: 3,
      slidesToScroll: 3,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            autoplay: true,
            autoplaySpeed: 5000,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 992,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            autoplay: true,
            autoplaySpeed: 5000,
            initialSlide: 2
          }
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 2,
            autoplay: true,
            autoplaySpeed: 5000,
            initialSlide: 2
          }
        }
      ]
    };
    return (
        <div className="blog-slider ">
         <Slider {...settings} >
            {
                this.state.BcardData.map(blogData=>{
                  return(
                    
                      <div className="blog-card" key={Math.random()} >
                            <Card cont={blogData.cont} Hbackground={blogData.Hbackground}>
                                <span>{blogData.smallTitle}</span>
                                <h4><a href="#">{blogData.titleLink} </a></h4>
                            </Card>
                      </div>
                    
                  )
              })
            }
          </Slider>
        </div>
      
      
    );
  }
}