import React from 'react'
import Icon from 'react-icofont'
import './QuestionItem.scss'
import style from './backgroundQues.module.scss'

const QuestionItem = (props) => {
    return (
        <div className="question-item">
           <div className="question">
                <p className={props.index===props.id ? style.backgroundQ:undefined}  onClick={()=>props.openAnswer(props.index)}>
                        <Icon icon={props.index===props.id ? props.arrowDown: props.arrowRight }/>  {props.question}
                    </p>
           </div>
            
            
            { props.index===props.id &&  <p className="answer" > {props.answer} </p>}
        </div>
    ) 
}

export default QuestionItem
