import React, { Component } from 'react'
import {Formik , Field , ErrorMessage} from 'formik'
import * as Yup from 'yup'
import './FormContact.scss'


export default class FormContact extends Component {


    form=(props)=>{
       return(
           <form onSubmit={props.handleSubmit}>
              <label>Name</label>
              <Field name="name" type="text" className="feild"/>
              <ErrorMessage name="name"/>

              <label>Email</label>
              <Field name="email" type="email" className="feild"/>
              <ErrorMessage name="email"/>

              <label>Phone Number</label>
              <Field name="phoneNumber" type="text" className="feild"/>
              <ErrorMessage name="phoneNumber" />
 
              <label>Message</label>
              <Field name="message" component="textarea" />

              <button className="btn mainButton" type="submit">Send Message</button>
           </form>
       )
    }

     schema=()=>{
         const schema = Yup.object().shape({
             name:Yup.string().required(),
             email:Yup.string().required(),
             phoneNumber:Yup.string().required()
         });
         return schema;
     }

    render() {
        return (
            <div className="form-contact">
                <h3>Stay Connected</h3>
                <Formik initialValues={{
                    name:"" , email:"" , phoneNumber:""
                 }} render={this.form} validationShema={this.schema}/>
            </div>
        )
    }
}
