import React ,{Component,createRef} from 'react'
import Icofont from 'react-icofont'
import './ScrollTop.scss'


class ScrollTop extends Component {

    state={
        scrollt:false
    }

    constructor(props){
        super(props)
        this.scrollArraw=createRef();
    }
    

    scrollToppage=()=>{
   
        if (document.body.scrollTop > 800 || document.documentElement.scrollTop > 800) {
            this.scrollArraw.current.style.visibility='visible';
          } else {
            this.scrollArraw.current.style.visibility = "hidden";
          }
    }
    
       
    componentDidMount(){
        window.addEventListener('scroll',()=>this.scrollToppage())
    }
  

    render(){
        
        return (
            <div className="scroll-top">
               <a href='#head' ref={this.scrollArraw} > <Icofont icon='rounded-up' className='arrow-up'  /> </a>
            </div>
        )
    }
}

export default ScrollTop
