import React,{Fragment} from 'react'
import WelcomeAddax from './WelcomeAddax/WelcomeAddax'
import AboutAddax from './AboutAddax/AboutAddax'
import AboutStrategy from './AboutStrategy/AboutStrategy'
import WhoWe from './WhoWe/WhoWe'
import BestBusiness from './BestBusiness/BestBusiness'
import Skills from './Skills/Skills'
import CountupAddax from './CountupAddax/CountupAddax'
import TeamAddax from './TeamAddax/TeamAddax'
import HowWork from './HowWork/HowWork'
import ServicesAddax from './ServicesAddax/ServicesAddax'
import WeDifferent from './WeDifferent/WeDifferent'
import OurWork from './OurWork/OurWork'
import OurProducts from './OurProducts/OurProducts'
import AskedQuestion from './AskedQuestion/AskedQuestion'
import OurClients from './OurClients/OurClients'
import BlogPosts from './BlogPosts/BlogPosts'
import PartnerArea from './PartnerArea/PartnerArea'
import Newsletter from './Newsletter/Newsletter'
import ContactUs from './ContactUs/ContactUs'
import { configureAnchors } from 'react-scrollable-anchor'

 
const Content = (props) => { 
    configureAnchors({offset: -87, scrollDuration: 200})
    return (
        <Fragment>
            <WelcomeAddax/>
            <AboutAddax />
            <AboutStrategy/>
            <WhoWe/>
            <BestBusiness/>
            <Skills/>
            <CountupAddax/>
            <TeamAddax/>
            <HowWork/>
            <ServicesAddax/>
            <WeDifferent/>
            <OurWork/>
            <OurProducts/>
            <AskedQuestion/>
            <OurClients/>
            <BlogPosts/>
            <PartnerArea/>
            <Newsletter/>
            <ContactUs/>
        </Fragment>
    )
}

export default Content
