import React from 'react'
import './Heading.scss'
import ScrollableAnchor from 'react-scrollable-anchor'

const Heading = () => { 
    return (
        <ScrollableAnchor id={'Home'}>
        <div className='heading' id="head">
            <div className="overlay text-center">
                <div className="container">
                    <h3>We Are Creative</h3>
                    <h1>The Best <span>Digital</span> Experience</h1>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt<br/> ut labore et dolore magna aliqua.</p>
                    <button type="button" className="btn mainButton">Get Started</button>
                    <button type="button" className="btn secondButton">View Work</button>
                </div> 
            </div>
        </div>
        </ScrollableAnchor>
    )
}

export default Heading
