import React, { Component } from 'react'
import './OurProducts.scss'
import PricingItem from './PricingItem/PricingItem'
import ScrollableAnchor from 'react-scrollable-anchor'


export default class OurProducts extends Component {

    state={
        productItem:[
            {title:'BASIC',
             price:'39.00',
            items:['Any Ware Access','10 GB Hosting' , ' 2 Unique Users' ,' 12 GB Capacity',' Weekly Backups',' Support 24 / hour']},

            {title:'STANDARD',
             price:'49.00',
            items:['Any Ware Access','15 GB Hosting' , ' 5 Unique Users' ,' 17 GB Capacity',' Weekly Backups',' Support 24 / hour']},

            {title:'PREMIUM',
             price:'59.00',
            items:['Any Ware Access','20 GB Hosting' , ' 10 Unique Users' ,' 21 GB Capacity',' Weekly Backups',' Support 24 / hour']}
        ],
        responsive:'col-12'
    }

    render() {
        return (
            <ScrollableAnchor id={'Price'}>
                <div className="our-products padding" id="ourProducts-a">
                    <div className="container">
                        <div className="ourProducts-cont cont">
                            <h2>Our Products <span>Pricing</span></h2>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor<br/> incididunt ut labore et dolore magna aliqua.</p>
                        </div>

                        <div className="product-pricing row">
                            {
                                this.state.productItem.map(pitem=>{
                                    return(
                                        <div className="col-lg-4 col-md-6" key={Math.random()}>
                                            <PricingItem title={pitem.title} price={pitem.price} items={pitem.items} responsive={this.state.responsive}/>
                                        </div>
                                        
                                    )
                                })
                            } 
                        </div>
                    </div>
                </div>
            </ScrollableAnchor>
        )
    }
}
