import React from 'react'
import Icofont from 'react-icofont'
import './Card.scss'

const Card = (props) => {
    return ( 
        <div className="w-card "> 
            {props.children}
            <Icofont icon={props.icon} size='3'  className="icon"/> 
            <span className="spanTitle">{props.step}</span>
            <div className="contentCard">
                <h4 className="title">{props.title}</h4>
                <p>{props.cont}</p>
            </div>
            
            <Icofont icon="arrow-right"  className="arrow"/>
            
        </div>
    )  
}

export default Card
 