import React from 'react'
import Icofont from 'react-icofont';
import CountUp from 'react-countup';
import { InView } from 'react-intersection-observer'

const CountUpA = (props) => {
   

    return (
        
          <InView triggerOnce="true">
            {({ inView, ref }) => (
              <div ref={ref} className="row" >
                {
                    props.countAddax.map(count=>{
                        return <div className="countup-div col-md-3 col-6" key={Math.random()} >
                                    <div className="icon">
                                        <Icofont  icon={count.icon} size="3"/>
                                    </div>
                                    <div className="count-cont">
                                   
                                        
                                            
                                        <CountUp  end={ inView ? count.countNum : 0 } className="count-num" duration={0}  useEasing={true}/>
                                        
                                        <p>{count.title}</p>
                                    </div>
                                </div>
                    })
                }
                </div>
            )}  
                </InView>
    
    )
}

export default CountUpA
