import React, { Component} from 'react'
import SecondCord from '../../../UI/SecondCard/SecondCord'

 
export default class WhoWeCards extends Component {

    state={ 
        cardData:[
            {icon:'users', title:'We are Professional' , span:'1' , cont:'Fringilla augue at maximus vestibulum. Nam pulvinar vitae neque et porttitor. Praesent sed nisi eleifend.'},
            {icon:'light-bulb', title:'We are Passionate' , span:'2' , cont:'Fringilla augue at maximus vestibulum. Nam pulvinar vitae neque et porttitor. Praesent sed nisi eleifend.'},
            {icon:'tag', title:'We are Creative' , span:'3' , cont:'Fringilla augue at maximus vestibulum. Nam pulvinar vitae neque et porttitor. Praesent sed nisi eleifend.'},
            {icon:'cut', title:'We are Designer' , span:'4' , cont:'Fringilla augue at maximus vestibulum. Nam pulvinar vitae neque et porttitor. Praesent sed nisi eleifend.'},
            {icon:'cubes', title:'We are Unique' , span:'5' , cont:'Fringilla augue at maximus vestibulum. Nam pulvinar vitae neque et porttitor. Praesent sed nisi eleifend.'},
            {icon:'life-buoy', title:'We are Support' , span:'6' , cont:'Fringilla augue at maximus vestibulum. Nam pulvinar vitae neque et porttitor. Praesent sed nisi eleifend.'}
        ],
        responsive:'col-12'
    } 
      
    render() { 
        return (
            <div className="row"> 
                {
                    this.state.cardData.map(data=>{
                        return (
                        <div className="col-xl-4 col-md-6 " key={data.span}>
                            <SecondCord responsive={this.state.responsive} icon={data.icon} title={data.title} cont={data.cont} span={data.span}/>
                         </div>)
                    })
                }
            </div>
        )
    }
}
