import React, { Component} from 'react'
import Card from '../../../UI/Card/Card'
import './HowWorkCard.scss'

 
export default class HowWorkCard extends Component {

    state={ 
        workCard:[
            { title:'Planing' , cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.', step:'01.' },
            { title:'Designing' , cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.', step:'02.'},
            {title:'Product Launch' , cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.', step:'03.'}
        ]

    
    }

    render() { 
        return (
            <div className="row"> 
                {
                    this.state.workCard.map(dataw=>{
                        return <div className="col-lg-4 col-md-6 " key={Math.random()}>
                            <Card  step={dataw.step} title={dataw.title} cont={dataw.cont}/>
                        </div>
                    })
                }
            </div>
        ) 
    }
}
