 import React, { Component, createRef } from 'react'
 import './WeDifferent.scss'
 import ItemShuffle from './ItemShuffle/ItemShuffle'
 import d1 from '../../../images/different/different1.jpg'
 import d2 from '../../../images/different/different2.jpg'
 import d3 from '../../../images/different/different3.jpg'
 import d4 from '../../../images/different/different4.jpg'
 import style from './styledButtons.module.scss'
 
 export default class WeDifferent extends Component {

    state={
        itemShuffle:[
            {title:'Speed , flexibility' ,
             cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco.',
             items:['Creative Design','Retina Ready','Responsive Design','Modern Design','Awesome Design','Digital Marketing & Branding'] , 
             img:d1},

             {title:'Profeesional work' , 
             cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco.',
             items:['Creative Design','Retina Ready','Responsive Design','Modern Design','Awesome Design','Digital Marketing & Branding'] , 
             img:d2},

             {title:'Experienced staff' , 
             cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco.',
             items:['Creative Design','Retina Ready','Responsive Design','Modern Design','Awesome Design','Digital Marketing & Branding'] , 
             img:d3},

             {title:'24/7 support' , 
             cont:'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco.',
             items:['Creative Design','Retina Ready','Responsive Design','Modern Design','Awesome Design','Digital Marketing & Branding'] , 
             img:d4}
            
        ],
     
        responsve:'col-12',
        id:'0',
        selected:0,
        buttons:[{ index:0,name:'Speed , flexibility'}, {index:1,name:'Profeesional work'},{index:2,name:'Experienced staff'},{index:3,name:'24/7 support'}]
    }


    constructor(props){
        super(props);

        this.button=createRef();
    
    }

    click=(index)=>{
        
        this.setState({
            id:index,
            selected:index
        })
    }
     render() {

        

         return (
        
             <div className="we-different padding">
                 <div className="container">
                     <div className="weDifferet-cont cont">
                        <h2>Why We <span>Are Different</span></h2>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor<br/> incididunt ut labore et dolore magna aliqua.</p>
                     </div>
                     <div className="weDifferet-shuffle">

                        <div className="buttons-shuffle">
                            {
                                this.state.buttons.map(button=>{
                                return(
                                    <button key={button.index} className={this.state.selected === button.index ? style.secondButton : style.firstButton} type="button" onClick={()=>this.click(button.index)} >{button.name}</button>
                                )
                            })
                            }
                        </div> 

                        <div className="content-shuffle">   
                            <ItemShuffle itemShuffle={this.state.itemShuffle} id={this.state.id} responsive={this.state.responsve} />
                        </div>                            
                       
                     </div>
                 </div>
             </div>
             
         )
     }
 }
 