import React from 'react'
import Icofont from 'react-icofont'
import './SecondCard.scss'


const SecondCard = (props) => {
    return (
        <div className="wh-card " >
            <Icofont icon={props.icon} size='3'  className="icon"/> 
            <div className="row">
                <div className="col-5">
                     <img src={props.img}/>
                </div>
                <div className={props.responsive}>
                    <h4>{props.title}</h4>
                    <span className="jT">{props.jobTitle}</span>
                </div>
            </div>
            
            
            <p>{props.cont}</p>
            <span className="backgSpan">{props.span} <Icofont icon={props.backgIcon}/></span>
        </div>

    )
}

export default SecondCard
