import React, {Component} from 'react'
import aboutStrategy from '../../../images/our-story.jpg'
import ListItems from '../../UI/ListItems/ListItems'
import './AboutStrategy.scss'

class AboutStrategy extends Component {

    state={
        items:['Creative Design','Retina Ready','Responsive Design','Modern Design','Awesome Design','Digital Marketing & Branding'],
        responsive:'col-12'
    }

    render(){
        return (
            <div className="about-strategy ">
                <div className="row">
                    <div className="col-md-6">
                        <div className="image">
                            <img src={aboutStrategy} alt="img"/>
                        </div> 
                    </div>
                    
                    <div className="col-md-6 padding">
                        <div className="container">
                            <p className="sp">Highly Creative Solutions</p>
                            <h2>About <span>Strategy</span></h2>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor<br/> incididunt ut labore et dolore magna aliqua.</p>
                            <ListItems items={this.state.items} responsive={this.state.responsive}/>
                            <button className="btn mainButton">Read More</button>
                        </div>
                    </div>
                    
                </div>
            </div>
        )

    }
}

export default AboutStrategy
